.card-header .card-title {
  line-height: 31px;
}

#thumbnail {
  width: 150px;
  height: 150px;
  background-repeat: no-repeat;
  background-size: contain;
  border: 1px solid #6c757d;
  border-radius: 5px;
}

#thumbnail-wrapper {
  flex-direction: row;
  gap: 10px;
  display: flex;
}

#thumbnail-wrapper .d-flex {
  gap: 10px;
}

#txt-preview {
  display: none;
}

.card-footer button {
  padding: 5px 10px;
}

#btn-clear {
  margin-left: 5px;
}

.book-preview {
  width: 60px;
}

tbody tr {
  cursor: pointer;
}

tbody tr td:last-child {
  width: 20px;
  text-align: center;
  vertical-align: middle;
  font-size: 1.3rem;
}

tbody tr td:last-child:hover {
  color: red;
}

tr.selected {
  background-color: #2c3e50 !important;
}

.book-preview {
  width: 60px;
  height: 80px;
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
}

.book-preview.no-image {
  background-image: url("no-image.3f2484cc.jpg");
}

